import { useState, useEffect, useCallback } from 'react';
import api from '@/services/api';
import { AxiosError } from 'axios'; // ðŸ‘ˆ Hata tipleri iÃ§in gerekli

// 1. Doktor verisinin tipini tanÄ±mlÄ±yoruz (any[] hatasÄ±nÄ± Ã§Ã¶zer)
export interface Doctor {
  id: number;
  firstName: string;
  lastName: string;
  email: string;
  role: string;
  createdAt: string; // Tarih string olarak gelir
}

// Formdan gelecek verilerin tipi
export interface CreateDoctorData {
  firstName: string;
  lastName: string;
  email: string;
  password: string;
}

export function useAdminDashboard() {
  // ðŸš¨ DÃœZELTME 1: any[] yerine Doctor[] kullandÄ±k
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  
  const [stats, setStats] = useState({
    totalDoctors: 0,
    totalPatients: 0,
    totalAppointments: 0,
    systemHealth: 98,
  });
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchData = useCallback(async () => {
    try {
      setIsLoading(true);
      const [doctorsRes, statsRes] = await Promise.all([
        api.get<Doctor[]>('/users/doctors'), // Gelen verinin tipini belirttik
        api.get('/stats/dashboard').catch(() => ({ data: null }))
      ]);

      setDoctors(doctorsRes.data);
      
      setStats(statsRes.data || { 
        totalDoctors: doctorsRes.data.length, 
        totalPatients: 0, 
        totalAppointments: 0, 
        systemHealth: 100 
      });
      
      setError(null);
    } catch (err: unknown) { // ðŸš¨ DÃœZELTME 2: 'any' yerine 'unknown'
      console.error("Dashboard Veri HatasÄ±:", err);
      
      // HatayÄ± gÃ¼venli bir ÅŸekilde kontrol ediyoruz
      if (err instanceof AxiosError) {
         if (err.response?.status !== 401) {
            setError('Veriler yÃ¼klenirken bir sorun oluÅŸtu.');
         }
      } else {
         setError('Beklenmedik bir hata oluÅŸtu.');
      }
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchData();
  }, [fetchData]);

  const addDoctor = async (data: CreateDoctorData) => {
    try {
      await api.post('/auth/register', {
        ...data,
        role: 'doctor', 
      });
      
      await fetchData();
      return { success: true };
    } catch (err: unknown) { // ðŸš¨ DÃœZELTME 3: 'any' yerine 'unknown'
      let message = 'Doktor eklenirken beklenmedik bir hata oluÅŸtu.';

      // Hata Axios hatasÄ± mÄ± diye kontrol et
      if (err instanceof AxiosError && err.response?.data) {
        // Backend'den gelen mesajÄ± gÃ¼venli ÅŸekilde al
        const errorData = err.response.data as { message?: string }; 
        if (errorData.message) {
           message = errorData.message;
        }
      }
      
      return { success: false, message };
    }
  };

  return { 
    doctors, 
    stats, 
    isLoading, 
    error, 
    refreshData: fetchData,
    addDoctor 
  };
}